import {
  AfterContentInit,
  ChangeDetectorRef,
  Component,
  ContentChildren,
  forwardRef,
  Input,
  OnDestroy,
  QueryList,
} from '@angular/core';
import { NG_VALUE_ACCESSOR } from '@angular/forms';
import { InputBaseComponent } from '@backbase/ui-ang/base-classes';
import { InputRadioComponent } from './input-radio.component';
import { DeprecationsService } from '@backbase/foundation-ang/future';

/**
 * @name InputRadioGroupComponent
 *
 * @a11y Current component provide option to pass needed accessibility
 * attributes. You need to take care of properties that are required in your case :
 *  - role
 *  - aria-activedescendant
 *  - aria-describedby
 *  - aria-expanded
 *  - aria-invalid
 *  - aria-label
 *  - aria-labelledby
 *  - aria-owns
 *
 * @description
 * Component that represents a group of radio buttons.
 */
@Component({
  selector: 'bb-input-radio-group-ui',
  templateUrl: './input-radio-group.component.html',
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => InputRadioGroupComponent),
      multi: true,
    },
  ],
})
export class InputRadioGroupComponent extends InputBaseComponent implements AfterContentInit, OnDestroy {
  /**
   * Set horizontal placement of the radio elements
   */
  @Input() horizontal = false;
  /**
   * Select first radio buttons onInit.
   */
  @Input() preselect = true;

  /**
   * The unique name of the radio group. This would the the name used for the descending radio buttons
   */
  @Input() name: string | undefined;

  /**
   * Vertical align of the radio buttons
   */
  @Input() verticalAlign: 'middle' | 'top' | 'bottom' = 'middle';

  /**
   * The child radio components of type InputRadioComponent
   */
  @ContentChildren(InputRadioComponent) radios: QueryList<InputRadioComponent> | undefined;

  private isComponentDestroyed = false;

  constructor(protected readonly cd: ChangeDetectorRef, protected readonly deprecationsService: DeprecationsService) {
    super(cd, deprecationsService);
  }

  ngAfterContentInit() {
    if (!this.preselect) {
      return;
    }
    setTimeout(() => {
      if (!this.val && this.radios && this.radios.first) {
        this.val = this.radios.first.value;
      }
    });
  }

  writeValue(value: any) {
    super.writeValue(value);

    if (!this.isComponentDestroyed) {
      this.cd.detectChanges();
    }
  }

  /**
   * Sets the value of the radio group with the provided value
   */
  set val(v: any) {
    this.value = v;
    this.onChange(this.value);
  }

  get val() {
    return this.value;
  }

  trackByFn(index: number, item: InputRadioComponent) {
    return item.value;
  }

  ngOnDestroy(): void {
    this.isComponentDestroyed = true;
  }
}
